import classes from './Cart.module.css';
import React from 'react';
import image2 from './../../img/image2.jpg'; 
import image6 from './../../img/image6.jpg';
import image4 from './../../img/image4.jpg'; 

const Cart =() =>{
    return(
        <div className={classes.Cart}>
            <div className={classes.Content}>
               <h1>World Class Make-up!</h1>
               <h4> WE ARE YOU AND WE CARE!</h4>
               <p>
                Our Bucket Item today is a face cream that works magic
                with acne, ecxyma and, wait for it, VITILIGO!
                Taste the best of both sides with US.
              </p>
            </div>
            <div className={classes.Photo}>
                 <img className={classes.Binary} src={image6} alt=''/>
                 <img className={classes.Binary} src={image4} alt=''/>
                 <img className={classes.Binary} src={image2} alt=''/>
            </div>
        </div>
    );
}

export default Cart;