import classes from './Contact.module.css';
import React from 'react';
import {FaYoutube, FaInstagram, FaLinkedin}  from "react-icons/fa";
  

const handleClick = (url) =>{
    return () =>window.open(url, '_blank')
  }
  

const Contact =() =>{
    return(
        <div className={classes.Contact}>
            <div className={classes.Content}>
            <h1>World Class Make-up!</h1>
            <h3>WE ARE YOU AND WE CARE!</h3>
            <p>
               Reach us from anywhere, anytime, we offer 24/7 services.
               Our social media has anything you need to know About
               Fille Boheme, follow us to get the first information
               about new products on launch as well as an education
               in regard to skin care.
            </p>
            </div> 
            <div className={classes.Icons}>
               <FaYoutube color='white' size='30px' style={{padding:'1%'}}
               onClick={handleClick('')} />
               <FaInstagram color='white' size='30px' style={{padding:'1%'}}
               onClick={handleClick('')} />
               <FaLinkedin color='white' size='30px' style={{padding:'1%'}}
                onClick={handleClick('')} />
            </div>
        </div>
    );
}

export default Contact;