import classes from './Home.module.css';
import React from 'react';
import {FaFacebook, FaInstagram, FaLinkedin,}  from "react-icons/fa";


const handleClick = (url) =>{
  return () =>window.open(url, '_blank')
}

const Home =() => {
    return (
      <div className={classes.Home}>
        <div className={classes.Container}>
          <h1 className={classes.Title}>FILLE BOHEME</h1>
          <h2>LIPS THAT WO'NT QUIT! </h2>
          <h6> WE ARE YOU AND WE CARE!</h6>
          <p>
            Your ideal kit for an ideal day
            with porcelain skin and bewitching lips!
          </p>
          <button className={classes.Button} id="Cart">SHOP NOW</button>
        </div>
        
        <div className={classes.Footer}>
          <div className={classes.Content}></div>
               <FaFacebook color='rgb(54, 28, 36)' size='30px' style={{padding:'1%'}}
               onClick={handleClick('')} />
               <FaInstagram color='rgb(54, 28, 36)' size='30px' style={{padding:'1%'}}
               onClick={handleClick('https://www.instagram.com/faydelores/')} />
               <FaLinkedin color='rgb(54, 28, 36)' size='30px' style={{padding:'1%'}}
                onClick={handleClick('https://www.linkedin.com/in/faith-mutuku-developer')} />
                <div>
                <p>Author: Faith Mutuku</p>
                <p>&copy; Copyright 2021, Grandeur</p>
                </div>
        </div>

      </div>
    );
  }
  
  export default Home;