import classes from './About.module.css';
import React from 'react';
  

const About =() =>{
    return(
        <div className={classes.About}>
            <div className={classes.Container}></div>
            <h1>World Class Make-up!</h1>
            <p>
                WE ARE YOU AND WE CARE!
                Fille Boheme is a Herbal Cosmetics Company whose sole mission
                is to encourage the natural look, with just the slightest
                touch of magic.
                Based in Utawala, Nairobi, our customers are of all
                cultures and heritage.
            </p>
        </div>
    );
}

export default About;