import classes from './Accessories.module.css';
import React from 'react';

const Accessories =() =>{
    return(
        <div className={classes.Accessories}>
            <div className={classes.Container}>
                <h1>Stock-up on Fille Boheme</h1>
                <p>
                    Our herbal made cosmetics are known to cure and conceal skin blemishes,
                    even out your complexion and leave you with porcelain skin.
                    High in moisturizer and added glycerine for lasting effects.
                    infused with aloe for rejuvination, you are guaranteed a soft,
                    smooth skin round the clock.
                </p>
            </div>
        </div> 
    );
}

export default Accessories;